import React from "react";
import { Form, Formik } from "formik";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import * as Yup from "yup";
import Logo from "../../assets/images/loginLogo.svg";
import "./Login.scss";
import { Button } from "../common/buttons/Buttons";
import { Text } from "../common/forms/Forms";
import { logIn } from "../../store/auth-reducer";

const initialValues = {
  username: "",
  password: "",
};

const validationSchema = Yup.object({
  username: Yup.string().required("  "),
  password: Yup.string().required("  "),
});

const Login = ({ isAuth, isAuthInProgress, isAuthFailed, logIn }) => {
  if (isAuth === true) return <Redirect to="/admin/orders" />;

  const onLoginSubmit = (userData) => {
    logIn(userData);
  };
  return (
    <div className="login-wrapper">
      <div className="login">
        <img className="login__logo" src={Logo} alt="" />
        <Formik
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={onLoginSubmit}
        >
          {(formik) => {
            return (
              <Form className="login-form">
                <h3 className="login-form__label"></h3>
                {isAuthFailed && (
                  <span className="error-message">
                       
                  </span>
                )}
                <Text
                  name="username"
                  title=""
                  placeholder="admin@gmail.com"
                  type="text"
                />
                <Text name="password" title="" type="password" />
                <div className="login-form__footer">
                  <div className="login-form__request"> </div>
                  <Button
                    type="submit"
                    additionalStyles="button__admin button__login"
                    isLoading={isAuthInProgress}
                    isDisabled={!formik.isValid}
                  >
                    
                  </Button>
                </div>
              </Form>
            );
          }}
        </Formik>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth,
  isAuthInProgress: state.auth.isAuthInProgress,
  isAuthFailed: state.auth.isAuthFailed,
});

export default connect(mapStateToProps, { logIn })(Login);
